body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: linear-gradient(to left,#060b45, #0553a1);
  color: #ddd;
  padding: 50px;
}
h1{
  color: #eef;
  text-align: center;
  font-size: 28px;
  
}
.group{
    background: hsl(142, 86%, 57%);
    width: 180px;
    margin: 0 auto 20px;
    display: grid;
    grid-template-columns: 100px 80px;
   border-radius: 25px; 
}
input,select{
    background: none;
    border:0;
    color:black;
    font-weight: bold;
    background: hsl(142, 86%, 57%);
    border-radius: 25px;
}
input{
    padding-left: 10px;
}
select{
    padding: 15px;
    
}
