.group{
    background: hsl(142, 86%, 57%);
    width: 180px;
    margin: 0 auto 20px;
    display: grid;
    grid-template-columns: 100px 80px;
   border-radius: 25px; 
}
input,select{
    background: none;
    border:0;
    color:black;
    font-weight: bold;
    background: hsl(142, 86%, 57%);
    border-radius: 25px;
}
input{
    padding-left: 10px;
}
select{
    padding: 15px;
    
}